.container-main{
    background-color: rgb(252, 169, 204);
    color: rgb(146, 61, 96);
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 65px;
    padding: 0 7%;
}

.container-main ul{
    list-style: none;
    display: flex;

}

.container-main ul li{
    padding: 0 20px;
}

.container-main ul li a{
    text-decoration: none;
    color: rgb(146, 61, 96);
}