.container-service {
    max-width: 450px;
    box-shadow: 0 0 0 3px rgba(0, 0, 0, 0.2);
    border-radius: 25px;
    margin: 2%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.container-service img {
    max-width: 130px;
    border-radius: 30px;
    margin: 9px 9px;
}

.container-service p {
    margin: 20px 0;
}