.conteiner-home {
    height: calc(100vh - 130px);
    display: flex;
    align-items: center;
    justify-content: center;
    
}

.conteiner-home img {
    max-width: 30%;
}

.conteiner-home div {
    max-width: 500px;
    margin-left: 90px;
}

.conteiner-home div h1 {
    margin: 20px 50px;
}

.conteiner-home div p {
    margin: 20px 0;
    max-width: 80%;
    line-height: 1.5;
}

.conteiner-home div button {
    background-color: rgb(146, 61, 96);
    color: black;
    width: 150px;
    border: none;
    border-radius: 25px;
    height: 35px;
    cursor: pointer;
    margin: 10px 120px;
}