.container-contato {
    height: calc(100vh - 130px);
    display: flex;
    align-items: center;
    justify-content: center;
}

.subcontainer-1{
    align-items: center;
    justify-content: center;
    display: flex;
}
.subcontainer-1 img {
    max-width: 30%;
}
.subcontainer-2{
    align-items: center;
    justify-content: center;
    display: flex;
    flex-direction: column;
}
.subcontainer-3{
    align-items: center;
    justify-content: center;
    display: flex;
}
.subcontainer-3 img {
    max-width: 30%;
}